<template><section><h1>animation 组件</h1>
<blockquote>
<p>基础用法</p>
</blockquote>
<demo-block>
                  <div class="source" slot="source"><template>
    <switch-component :xchange="switchAnimationChange" :init-data="switchInitDataAnim" :checked-type="checkedAnimationType"></switch-component> {{checkedAnimationType}}
        <br><br><br><br><br><br>
    <animation :animated="checkedAnimationType" :animated-one="animatedOne" :animated-one-enter-fn="animatedOneEnterFn" :animated-one-over-fn="animatedOneOverFn">
    </animation><br>

    <t-animation :t-style="{
        enter: {
            time: '0.5s',
            animationType: 'linear',
            from: {
                opacity: 0
            },
            to: {
                opacity: 1
            }
        },
        leave: {
            time: '1s',
            animationType: 'linear',
            from: {
                opacity: 1
            },
            to: {
                opacity: 0
            }
        }
        }" :init-style="{
            opacity: 0,
            transition: 'opacity linear 0.2s'
        }" :t-switch="checkedAnimationType" :t-animation-event="tAnimationType">
        <img src="static/logo.png" alt="logo">
    </t-animation>
</template>

</div>
                  <h3>通过<code>传参</code>的方式实现动画</h3>

                  <div class="highlight" slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">switch-component</span> 
        <span class="hljs-attr">:xchange</span>=<span class="hljs-string">"switchAnimationChange"</span> 
        <span class="hljs-attr">:init-data</span>=<span class="hljs-string">"switchInitDataAnim"</span> 
        <span class="hljs-attr">:checked-type</span>=<span class="hljs-string">"checkedAnimationType"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">switch-component</span>&gt;</span> {{checkedAnimationType}}
        <span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">animation</span>
        <span class="hljs-attr">:animated</span>=<span class="hljs-string">"checkedAnimationType"</span> 
        <span class="hljs-attr">:animated-one</span>=<span class="hljs-string">"animatedOne"</span>
        <span class="hljs-attr">:animated-one-enter-fn</span>=<span class="hljs-string">"animatedOneEnterFn"</span>
        <span class="hljs-attr">:animated-one-over-fn</span>=<span class="hljs-string">"animatedOneOverFn"</span>
    &gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">animation</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">br</span>/&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">t-animation</span>
        <span class="hljs-attr">:t-style</span>=<span class="hljs-string">"{
        enter: {
            time: '0.5s',
            animationType: 'linear',
            from: {
                opacity: 0
            },
            to: {
                opacity: 1
            }
        },
        leave: {
            time: '1s',
            animationType: 'linear',
            from: {
                opacity: 1
            },
            to: {
                opacity: 0
            }
        }
        }"</span>
        <span class="hljs-attr">:init-style</span> = <span class="hljs-string">"{
            opacity: 0,
            transition: 'opacity linear 0.2s'
        }"</span>
        <span class="hljs-attr">:t-switch</span>=<span class="hljs-string">"checkedAnimationType"</span>
        <span class="hljs-attr">:t-animation-event</span>=<span class="hljs-string">"tAnimationType"</span>
    &gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"static/logo.png"</span> <span class="hljs-attr">alt</span>=<span class="hljs-string">"logo"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">t-animation</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { Animation, TAnimation, SwitchComponent } <span class="hljs-keyword">from</span> <span class="hljs-string">'vo-ui'</span>
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">checkedType</span>: <span class="hljs-literal">true</span>,
            <span class="hljs-attr">checkedAnimationType</span>: <span class="hljs-literal">true</span>, <span class="hljs-comment">// 动画组件是否全部 开始播放</span>
            animatedOne: <span class="hljs-number">-1</span>, <span class="hljs-comment">// 动画组件是否无线循环</span>
            tAnimationType: <span class="hljs-string">'enter'</span>, <span class="hljs-comment">// 动画是进入(enter)  还是  离开(leave)</span>
        };
    },
    <span class="hljs-attr">components</span>: {
        Animation, TAnimation, SwitchComponent
    },
    <span class="hljs-attr">methods</span>: {
        <span class="hljs-comment">// 初始化给的 开关数据</span>
        switchInitData(e) {
            <span class="hljs-keyword">this</span>.checkedType = e
        },
        <span class="hljs-comment">// 动画事件</span>
        switchAnimationChange(e) {
            <span class="hljs-keyword">this</span>.checkedAnimationType = !e;
        },
        switchInitDataAnim(e) {
            <span class="hljs-keyword">this</span>.checkedAnimationType = e
        },
        <span class="hljs-comment">// 鼠标移入 某个动画元素</span>
        animatedOneEnterFn(e) {
            <span class="hljs-keyword">this</span>.animatedOne = e
        },
        <span class="hljs-comment">// 鼠标移除 某个动画元素关闭动画</span>
        animatedOneOverFn() {
            <span class="hljs-keyword">this</span>.animatedOne = <span class="hljs-number">-1</span>
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<blockquote>
<h2>开关配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>switchAnimationChange</td>
<td style="text-align:left">开关事件</td>
</tr>
<tr>
<td>switchInitDataAnim</td>
<td style="text-align:left">初始化开关状态，是否打开</td>
</tr>
<tr>
<td>checkedAnimationType</td>
<td style="text-align:left">开关数据</td>
</tr>
</tbody>
</table>
<blockquote>
<h2>动画默认</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>checkedAnimationType</td>
<td style="text-align:left">动画是否开始</td>
</tr>
<tr>
<td>animatedOne</td>
<td style="text-align:left">-1动画组件是否无线循环</td>
</tr>
<tr>
<td>animatedOneEnterFn</td>
<td style="text-align:left">事件， 鼠标移入 某个动画元素</td>
</tr>
<tr>
<td>animatedOneOverFn</td>
<td style="text-align:left">事件， 鼠标移除 某个动画元素关闭动画</td>
</tr>
</tbody>
</table>
<blockquote>
<h2>动画可配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:t-style</td>
<td style="text-align:left">给动画创建样式，必填</td>
</tr>
<tr>
<td>:t-style  &gt;  enter</td>
<td style="text-align:left">进入， 非必填</td>
</tr>
<tr>
<td>:t-style  &gt;  leave</td>
<td style="text-align:left">离开， 非必填</td>
</tr>
<tr>
<td>:init-style</td>
<td style="text-align:left">初始化动画样式设置</td>
</tr>
<tr>
<td>:t-switch</td>
<td style="text-align:left">动画开关</td>
</tr>
<tr>
<td>:t-animation-event</td>
<td style="text-align:left">动画是进入(enter)  还是  离开(leave)</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import { Animation, TAnimation, SwitchComponent } from 'vo-ui'
export default {
    data() {
        return {
            checkedType: true,
            checkedAnimationType: true, // 动画组件是否全部 开始播放
            animatedOne: -1, // 动画组件是否无线循环
            tAnimationType: 'enter', // 动画是进入(enter)  还是  离开(leave)
        };
    },
    components: {
        Animation, TAnimation, SwitchComponent
    },
    methods: {
        // 初始化给的 开关数据
        switchInitData(e) {
            this.checkedType = e
        },
        // 动画事件
        switchAnimationChange(e) {
            this.checkedAnimationType = !e;
        },
        switchInitDataAnim(e) {
            this.checkedAnimationType = e
        },
        // 鼠标移入 某个动画元素
        animatedOneEnterFn(e) {
            this.animatedOne = e
        },
        // 鼠标移除 某个动画元素关闭动画
        animatedOneOverFn() {
            this.animatedOne = -1
        }
    }
};
</script>


